import {Layout, Menu} from "antd";
import {useState} from "react";
import ChangeInfo from "./ChangeInfo";
import ChangePass from "./ChangePass";
import ChangeEmail from "./ChangeEmail";
import GetInfo from "./GetInfo";
import OrderHistory from "../Order/OrderHistory";
import {Link, Route} from "react-router-dom";


export default function UserPage(page, users){
    const [info, setInfo] = useState("GetInfo");
    if(page === "UserPage" ) {
        const {Content, Sider} = Layout;

        const CallGetInfo = async () =>{
            setInfo("GetInfo");
        }

        const CallChangeInfo = async () =>{
            setInfo("ChangeInfo");
        }

        const CallChangePass = async () =>{
            setInfo("ChangePass");
        }

        const CallChangeEmail = async () =>{
            setInfo("ChangeEmail");
        }

        const CallOrderHistory = async () =>{
            setInfo("OrderHistory");
        }

        return (
            <Layout>
                <Sider width={200} className="site-layout-background">
                    <Menu
                        mode="inline"
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        style={{height: '100%', borderRight: 0}}
                    >
                        <Menu.Item key="1" onClick={CallGetInfo}>  <Link to="/user">Информация об аккаунте </Link></Menu.Item>
                        <Menu.Item key="2" onClick={CallChangeInfo}>  <Link to="/user/changeInfoForUser">Изменить данные </Link></Menu.Item>
                        <Menu.Item key="3" onClick={CallChangePass}>  <Link to="/user/changePass">Сменить пароль </Link></Menu.Item>
                        <Menu.Item key="4" onClick={CallChangeEmail}>  <Link to="/user/changeEmail">Сменить почту</Link></Menu.Item>
                        <Menu.Item key="5" onClick={CallOrderHistory}>  <Link to="/user/ordersHistory">История заказов </Link></Menu.Item>
                    </Menu>
                </Sider>
                <Layout style={{padding: '0 24px 24px'}}>
                    <Content
                        className="site-layout-background"
                        style={{
                            padding: 24,
                            margin: 0,
                            minHeight: 280,
                        }}
                    >
                        {GetInfo(info, users)}
                        <Route path="/user/changeInfoForUser">{ChangeInfo(info)}</Route>
                        <Route path="/user/changePass">{ChangePass(info)}</Route>
                        <Route path="/user/changeEmail">{ChangeEmail(info)}</Route>
                        <Route path="/user/ordersHistory"><OrderHistory info={info} /></Route>
                    </Content>
                </Layout>
            </Layout>
        )
    }
}