import React from "react";
import { Typography } from "antd";
//axios.get("http://localhost:8081/product/get?id=" + product).then(response => {return (<div>{response.data}</div>)})

export default class SalesUnitCard extends React.Component{
    state = {
        loading: true
    }

    componentDidMount() {
        this.setState({ loading: false });
    }


    render() {
        if (this.state.loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        const { Title } = Typography;

        return (
            <div>
                <Title level={3}>{this.props.unit.product.name + ' ' + this.props.unit.product.model}</Title>
                <Title level={5}>Описание: {this.props.unit.desc}</Title><br/>
                <Title level={5}>Количество: {this.props.unit.quantity}</Title><br/>
                <Title level={5}>Цена товара: {this.props.unit.price}</Title><br/>
                <Title level={5}>Цена доставки: {this.props.unit.shipCost}</Title><br/>
            </div>
        )

    }

}