import React from "react";
import axios from "axios";
import { Comment, Rate, Tooltip } from "antd";
import moment from "moment";

export default class Feedback extends React.Component{
    state = {
        user: [],
        loading: true
    }

    componentDidMount() {
        axios.get("http://localhost:8081/users/getUserById?id=" + this.props.fb.userId)
            .then(res => {
                const user = res.data;
                this.setState({ user: user, loading: false });
            })
    }


    render() {
        if (this.state.loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        return (
            <Comment
                author={this.state.user.username}
                content={
                    <p>
                        {this.props.fb.text}
                        <Rate disabled defaultValue={this.props.fb.rating} />
                    </p>
                }
                datetime={
                    <Tooltip title={this.props.fb.dateCreated}>
                        <span>{moment().fromNow()}</span>
                    </Tooltip>
                }
            />
        )

    }

}