import {useState} from "react";
import axios from "axios";
import {Button, Carousel, Col, Descriptions, Form, Image, Input, Layout, Rate, Row, Typography} from "antd";
import ProductCard from "../Product/ProductCard";
import CreateOrder from "../Order/CreateOrder";
import {useHistory} from "react-router-dom";


export default function SalesUnitPage(page, salesUnits, userId, CallUserPage) {
    const [loading, setLoading] = useState(true);
    const [pictures, setPictures] = useState();
    const [charact, setCharact] = useState();
    const [recommended, setRecommended] = useState();
    const [visible, setVisible] = useState(false);
    const [salesUnit, setSalesUnit] = useState();
    const [shop, setShop] = useState();
    const {Title} = Typography;
    const {Content} = Layout;

    function getPictures(productId) {
        return axios.get("http://localhost:8081/product/getAllPictures?id=" + productId);
    }

    function getCharact(productId) {
        return axios.get("http://localhost:8081/characteristic/getForProduct?id=" + productId);
    }

    function getShop(shopId) {
        return axios.get("http://localhost:8081/shops/findById?id=" + shopId);
    }

    function getRecommended() {
        return axios.get("http://localhost:8081/product/getRecommended");
    }

    if(salesUnits != null) {
        if (loading) {
            axios.get("http://localhost:8081/salesUnit/get?id=" + salesUnits).then(response => {
                setSalesUnit(response.data)
                axios.all([getPictures(response.data.product.id), getCharact(response.data.product.id), getShop(response.data.shopId), getRecommended()]).then(
                    axios.spread(function (pictures,
                                           characts,
                                           shop,
                                           recommended) {
                        setPictures(pictures.data);
                        setCharact(characts.data);
                        setShop(shop.data);
                        setRecommended(recommended.data)
                        setLoading(false);
                    })
                )
            })
        }

        if (loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        return (
            <Content
                className="site-layout-background"
                style={{
                    padding: 24,
                    margin: 0,
                    minHeight: 280,
                }}
            >
                <Row>
                    <Col span={10} offset={1}>
                        <Image
                            preview={{visible: false}}
                            width={400}
                            height={400}
                            src={"http://localhost:8081/product/getPicture?id=" + pictures[0]}
                            onClick={() => setVisible(true)}
                        />
                        <div style={{display: 'none'}}>
                            <Image.PreviewGroup preview={{visible, onVisibleChange: vis => setVisible(vis)}}>
                                {pictures &&
                                pictures.map(picture => {
                                    return (
                                        <Image src={"http://localhost:8081/product/getPicture?id=" + picture}/>
                                    )
                                })}
                            </Image.PreviewGroup>
                        </div>
                    </Col>
                    <Col offset={1}>
                        Магазин: {shop.name} <br/>
                        Наименование: {salesUnit.product.name + ' ' + salesUnit.product.model} <br/>
                        Описание: {salesUnit.product.desc} <br/>
                        Производитель: {salesUnit.product.manufacturer.name} <br/>
                        Артикул: {salesUnit.product.article} <br/>
                        Дата выхода: {salesUnit.product.dateReleased} <br/>
                        Габариты: {salesUnit.product.dimensions} <br/>
                        Вес: {salesUnit.product.weight} <br/>
                        Цена: {salesUnit.price} <br/>
                        Стоимость доставки: {salesUnit.shipCost} <br/>
                        Количество в магазине: {salesUnit.quantity} <br/>
                        <CreateOrder userId={userId} redir={CallUserPage} suID={salesUnit.id}/>
                    </Col>
                </Row>
                <Layout style={{padding: '24px 24px 24px'}}>
                    <Row>
                        <Col offset={1}>
                            <Descriptions title="Характеристики" bordered={true} column={1}>
                                {charact &&
                                charact.map(char => {
                                        return (
                                            <Descriptions.Item label={char[0]}>{char[1]}</Descriptions.Item>
                                        )
                                    }
                                )}
                            </Descriptions>
                        </Col>
                        <Col offset={1}>
                            <Title level={3}>Дефекты</Title>
                            {salesUnit.desc}
                        </Col>
                    </Row>
                </Layout>
                <Layout style={{padding: '24px 24px 24px'}}>
                    <Row>
                        <Col offset={6}>
                            <Title level={3}>Похожие товары</Title>
                            <Carousel autoplay>
                                {recommended &&
                                recommended.map(recom => {
                                        return (
                                            <div>
                                                <ProductCard url={"http://localhost:8081/product/get?id=" + recom.id}/>
                                            </div>
                                        )
                                    }
                                )}
                            </Carousel>
                        </Col>
                    </Row>
                </Layout>

            </Content>
        )
    }

}