import {Button, Form, Input, message} from "antd";
import axios from "axios";
import React from "react";

export default function ChangeShopInfo(info){

    const formItemLayout = {
        labelCol: {
            xs: {
                span: 24,
            },
            sm: {
                span: 8,
            },
        },
        wrapperCol: {
            xs: {
                span: 24,
            },
            sm: {
                span: 16,
            },
        },
    };

    const tailFormItemLayout = {
        wrapperCol: {
            xs: {
                span: 24,
                offset: 0,
            },
            sm: {
                span: 16,
                offset: 8,
            },
        },
    };

    const ChangeInfoForm = () => {
        const [form] = Form.useForm();

        const onFinish  = async (values) => {
            axios.post('http://localhost:8081/shops/changeInfo', values)
                .then(response => {
                    if(response.status === 200){
                        message.success("Данные успешно изменены")
                    }else{
                        message.error("Проверьте корректность данных")
                    }
                })
        };

        return (
            <Form
                {...formItemLayout}
                form={form}
                name="ChangeInfo"
                onFinish={onFinish}
                scrollToFirstError
            >

                <Form.Item
                    name="phoneNumber"
                    label="Номер телефона"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your phone number!',
                        },
                    ]}
                >
                    <Input
                        style={{
                            width: '100%',
                        }}
                    />
                </Form.Item>

                <Form.Item
                    name="workTime"
                    label="Часы работы"
                    tooltip="What do you want others to call you?"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your nickname!',
                            whitespace: true,
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    name="email"
                    label="Почта"
                    tooltip="What do you want others to call you?"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your nickname!',
                            whitespace: true,
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    name="address"
                    label="Адрес"
                    tooltip="What do you want others to call you?"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your nickname!',
                            whitespace: true,
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit">
                        Register
                    </Button>
                </Form.Item>
            </Form>
        );
    };
    return (<ChangeInfoForm />);


}