import React, {useState} from "react";
import axios from "axios";
import {Space} from "antd";
import OrderCardForShop from "../Order/OrderCardForShop";


export default class OrdersForShop extends React.Component{

    state = {
        orders: [],
        loading: true
    }

    componentDidMount() {
        axios.get("http://localhost:8081/order/getAll?id=" + this.props.shopId)
            .then(res => {
                const orders = res.data;
                this.setState({ orders: orders, loading: false });
            })
    }


    render() {


        if (this.state.loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        return (
            <Space wrap>
                {this.state.orders &&
                this.state.orders.map(order => {
                        return (
                            <OrderCardForShop order={order}/>
                        )
                    }
                )}
            </Space>
        )


    }
}