import React, {useState} from "react";
import axios from "axios";
import SalesUnitCard from "../SalesUnit/SalesUnitCard";
import {Space} from "antd";


export default class SUForShop extends React.Component{

    state = {
        units: [],
        loading: true
    }

    componentDidMount() {
        axios.get("http://localhost:8081/salesUnit/getAll?id=" + this.props.shopId)
            .then(res => {
                const salesUnit = res.data;
                this.setState({ units: salesUnit, loading: false });
            })
    }


    render() {


        if (this.state.loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        return (
            <Space>
                {this.state.units &&
                this.state.units.map(unit => {
                        return (
                            <SalesUnitCard unit={unit} />
                        )
                    }
                )}
            </Space>
        )


    }
}