import {Button, Form, Input, message} from "antd";
import axios from "axios";

export default function ChangePass(info){

        const formItemLayout = {
            labelCol: {
                xs: {
                    span: 24,
                },
                sm: {
                    span: 8,
                },
            },
            wrapperCol: {
                xs: {
                    span: 24,
                },
                sm: {
                    span: 16,
                },
            },
        };

        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };

        const ChangePassForm = () => {
            const [form] = Form.useForm();
            const onFinish  = async (values) => {
                axios.post('http://localhost:8081/users/changePass?pass=' + values.pass + '&pass2=' + values.pass2 + '&pass3=' + values.pass3)
                    .then(response => {
                        if(response.status === 200){
                            message.success("Пароль успешно измененен")
                        }else{
                            message.error("Неправильный пароль")
                        }
                    })
            };

            return (
                <Form
                    {...formItemLayout}
                    form={form}
                    name="changePass"
                    onFinish={onFinish}
                    scrollToFirstError
                >
                    <Form.Item
                        name="pass"
                        label="Текущий пароль"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                        ]}
                        hasFeedback
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item
                        name="pass2"
                        label="Повторите текущий пароль"
                        dependencies={['pass']}
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: 'Please confirm your password!',
                            },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('pass') === value) {
                                        return Promise.resolve();
                                    }

                                    return Promise.reject(new Error('The two passwords that you entered do not match!'));
                                },
                            }),
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item
                        name="pass3"
                        label="Новый пароль"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                        ]}
                        hasFeedback
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit">
                            Сменить почту
                        </Button>
                    </Form.Item>
                </Form>
            );
        };

        return (<ChangePassForm />);


}