import {Layout, Space, Tabs} from 'antd';
import {useState} from "react";
import axios from "axios";
import GetCardManufacturer from "../Manufacturer/GetCardManufacturer";
import ProductCard from "../Product/ProductCard";
import {Link} from "react-router-dom";
import ProductPage from "../Product/ProductPage";

export default function SearchPage(search, setProduct, CallProductPage) {
    const [loading, setLoading] = useState(true);
    const [products, setProducts] = useState();
    const [manufacturers, setManufacturers] = useState();

    function searchManufacturer(search) {
        return axios.get("http://localhost:8081/manufacturer/search?name=" + search);
    }

    function searchProduct(search) {
        return axios.get("http://localhost:8081/product/search?search=" + search);
    }

    const ProductPage = (product) => {
        setProduct(product);
        CallProductPage();
    }

    const {TabPane} = Tabs;

    if (search != null && loading) {
        axios.all([searchManufacturer(search), searchProduct(search)]).then(
            axios.spread(function (manufacturers,
                                   products
            ) {
                setManufacturers(manufacturers.data);
                setProducts(products.data)
                setLoading(false);
            })
        )

    }

    if (loading) {
        return (<p>Loading...</p>); // отображение загрузки
    }

    const Demo = () => (
        <Tabs defaultActiveKey="1">
            <TabPane tab="Товары" key="1">
                <Space>
                {products &&
                products.map(product => {
                        return (
                            <Link to="/product">
                                <div onClick={() => ProductPage(product.id)}>
                                    <ProductCard url={"http://localhost:8081/product/get?id=" + product.id}/>
                                </div>
                            </Link>
                        )
                    }
                )}
                </Space>
            </TabPane>
            <TabPane tab="Производители" key="2">
                <Space>
                {manufacturers &&
                manufacturers.map(manufacturer => {
                        return (
                            <div className="manufacturer">
                                {GetCardManufacturer(manufacturer)}
                            </div>
                        )
                    }
                )}
                </Space>
            </TabPane>
        </Tabs>
    );

    return (
        <Layout style={{padding: '24px 24px 24px'}}>
            <Demo/>
        </Layout>
    )
}