import {Layout, Menu} from "antd";
import {useState} from "react";
import axios from "axios";
import GetCardShop from "./GetCardShop";

export default function ShopsPage(page) {
    const [shops, setShops] = useState();
    const [flag, setFlag] = useState(false);
    const getShops = () => {
        axios.get("http://localhost:8081/shops/findAll")
            .then(response => {
                    setShops(response.data);
                    setFlag(true);
                }
            )
    }


    const {Content, Sider} = Layout;
    if (page === "ShopsPage") {
        if (flag === false) {
            getShops();
        }

        if (flag === true) {
            return (
                <Layout>
                    <Layout style={{padding: '0 24px 24px'}}>
                        <Content
                            className="site-layout-background"
                            style={{
                                padding: 24,
                                margin: 0,
                                minHeight: 280,
                            }}
                        >
                            <div className="shops">
                                {shops &&
                                shops.map((shop, index) => {
                                    return (
                                        <div className="shop" key={index}>
                                            {GetCardShop(shop)}
                                        </div>
                                    );
                                })}
                            </div>
                        </Content>
                    </Layout>
                </Layout>
            )
        }
    }
}