import {Layout, Menu} from "antd";
import GetCardManufacturer from "./GetCardManufacturer";
import {useState} from "react";
import axios from "axios";


export default function ManufacturerPage(page) {
    const [manufacturers, setManufacturers] = useState();
    const [flag, setFlag] = useState(false);
    const getManufacturers = () => {
        axios.get("http://localhost:8081/manufacturer/getAll")
            .then(response => {
                    setManufacturers(response.data);
                    setFlag(true);
                }
            )
    }


    const {Content, Sider} = Layout;

    if (flag === false) {
        getManufacturers();
    }

    if (flag === true) {
        return (
            <Layout>
                <Layout style={{padding: '0 24px 24px'}}>
                    <Content
                        className="site-layout-background"
                        style={{
                            padding: 24,
                            margin: 0,
                            minHeight: 280,
                        }}
                    >
                        <div className="manufacturers">
                            {manufacturers &&
                            manufacturers.map((manufacturer, index) => {
                                return (
                                    <div className="manufacturer" key={index}>
                                        {GetCardManufacturer(manufacturer)}
                                    </div>
                                );
                            })}
                        </div>
                    </Content>
                </Layout>
            </Layout>
        )
    }

}