import {Button, Card, Checkbox, DatePicker, Form, Modal, Select} from "antd";
import axios from "axios";
import {useState} from "react";
import {Option} from "antd/es/mentions";


export default function OrderCardForShop(order){
    const [loading, setLoading] = useState(true);
    const [salesUnit, setSalesUnit] = useState();
    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const onFinish = (values: any) => {
        values.deliveryDate = values.deliveryDate.format('YYYY-MM-DD')
        console.log('Success:', values);
    };
    if(loading){
        axios.get("http://localhost:8081/salesUnit/get?id=" + order.order.suId).then(response =>{
            setSalesUnit(response.data);
            setLoading(false);
        })
    }

    if (loading) {
        return (<p>Loading...</p>); // отображение загрузки
    }

    let mark = order.order.mark === 1
    if(salesUnit == null){
        return(
            <Card title="Товар был удален из магазина" style={{width: 300}}>
            </Card>
        )
    }else {
        return (
            <div>
                <Card title={salesUnit.product.name + ' ' + salesUnit.product.model} style={{width: 300}} onClick={showModal}>
                    <p>Статус: {order.order.status}</p>
                    <p>Дата доставки: {order.order.deliveryDate}</p>
                </Card>
                <Modal title={salesUnit.product.name + ' ' + salesUnit.product.model} visible={isModalVisible} onOk={handleOk}>
                    <p>Статус: {order.order.status}</p>
                    <p>Дата доставки: {order.order.deliveryDate}</p>
                    <p>Комментарий: {order.order.comm}</p>
                    <p>Адрес доставки: {order.order.address}</p>
                    <p>Доставка курьером: <Checkbox defaultChecked={mark} disabled /></p>
                    <Form
                        name="basic"
                        labelCol={{ span: 8 }}
                        wrapperCol={{ span: 16 }}
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                        autoComplete="off"
                    >
                        <Form.Item
                            label="Статус"
                            name="status"
                        >
                            <Select defaultValue="Processed" style={{ width: 120 }}>
                                <Option value="Processed">В обработке</Option>
                                <Option value="Shipped">Отгружено</Option>
                                <Option value="Received">Получено</Option>
                                <Option value="Canceled">Отменено</Option>
                            </Select>
                        </Form.Item>

                        <Form.Item
                            label="Дата доставки"
                            name="deliveryDate"
                        >
                            <DatePicker />

                        </Form.Item>

                        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                            <Button type="primary" htmlType="submit">
                                Submit
                            </Button>
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )
    }
}