import {Layout, Carousel, Row, Col, Image, Rate, Descriptions, Button, Typography, Form, Input, Checkbox} from "antd";
import {createRef, useState} from "react";
import axios from "axios";
import SalesUnitCarousel from "../SalesUnit/SalesUnitCarousel";
import moment from "moment";
import Feedback from "../Feedback/Feedback";
import ProductCard from "./ProductCard";
import {Link} from "react-router-dom";


export default function ProductPage(page, productId, CallSUPage, setSalesUnits) {
    const [loading, setLoading] = useState(true);
    const [product, setProduct] = useState();
    const [pictures, setPictures] = useState();
    const [charact, setCharact] = useState();
    const [salesUnit, setSalesUnit] = useState();
    const [feedback, setFeedback] = useState();
    const [recommended, setRecommended] = useState();
    const [rating, setRating] = useState();
    const [visible, setVisible] = useState(false);

    function getProduct() {
        return axios.get("http://localhost:8081/product/get?id=" + productId);
    }

    function getPictures() {
        return axios.get("http://localhost:8081/product/getAllPictures?id=" + productId);
    }

    function getCharact() {
        return axios.get("http://localhost:8081/characteristic/getForProduct?id=" + productId);
    }

    function getRecommended() {
        return axios.get("http://localhost:8081/product/getRecommended");
    }

    function getFeedbacks() {
        return axios.get("http://localhost:8081/feedback/getAllForProduct?id=" + productId);
    }

    function getSalesUnit() {
        return axios.get("http://localhost:8081/salesUnit/getForCity?id=" + productId);
    }

    function getRating() {
        return axios.get("http://localhost:8081/product/getRating?id=" + productId);
    }

    const carousel = createRef();

    const handleNext = () => carousel.current.next();

    const handlePrev = () => carousel.current.prev();

    const {Title} = Typography;

    const onFinish = (values) => {
        values.productId = productId;
        axios.put("http://localhost:8081/feedback/save", values)
    };

    const GetSUPage = (suId) => {
        setSalesUnits(suId);
        CallSUPage();
    }

    const contentStyle = {
        height: '160px',
        color: '#fff',
        lineHeight: '160px',
        textAlign: 'center',
        background: '#364d79',
    };
    //async добавить
    async function LoadData(){
        await axios.get("http://localhost:8081/product/get?id=" + productId).then( products => {
                setProduct(products.data);
                setLoading(false);
            }
        )
        await axios.get("http://localhost:8081/characteristic/getForProduct?id=" + productId).then(response => {
                setCharact(response.data);
            }).catch(
            setCharact(null)
        )
        await axios.get("http://localhost:8081/product/getAllPictures?id=" + productId).then(response => {
                setPictures(response.data);
            }
        ).catch(
            setPictures(null)
        )
        await axios.get("http://localhost:8081/feedback/getAllForProduct?id=" + productId).then(response => {
                setFeedback(response.data);
            }
        ).catch(
            setFeedback(null)
        )
        await axios.get("http://localhost:8081/product/getRecommended").then(response => {
                setRecommended(response.data);
            }
        ).catch(
            setRecommended(null)
        )
        await axios.get("http://localhost:8081/product/getRating?id=" + productId).then(response => {
                setRating(response.data);
            }
        ).catch(
            setRating(0)
        )
        await axios.get("http://localhost:8081/salesUnit/getForCity?id=" + productId).then(response => {
                setSalesUnit(response.data);
            }
        ).catch(
            setSalesUnit(null)
        )

    }

    if(productId != null){
    if (loading) {
        LoadData();
        /*axios.all([getProduct(), getPictures(), getCharact(), getRating(), getFeedbacks(), getSalesUnit(), getRecommended()])
            .then(axios.spread(function (products,
                                         pictures,
                                         characts,
                                         rating,
                                             feedbacks,
                                             salesUnits,
                                             recommendeds) {
                    setProduct(products.data);
                    setPictures(pictures.data);
                    setCharact(characts.data);
                    setRating(rating.data);
                    setFeedback(feedbacks.data);
                    setSalesUnit(salesUnits.data);
                    setRecommended(recommendeds.data)
                    setLoading(false);
                })
            )*/
    }
    const {Content} = Layout;
    if (loading) {
        return (<p>Loading...</p>); // отображение загрузки
    }
    if(page !== "ProductPage"){
        setLoading(true);
        productId = null;
    }
    return (
        <Content
            className="site-layout-background"
            style={{
                padding: 24,
                margin: 0,
                minHeight: 280,
            }}
        >
            <Row>
                <Col span={10} offset={1}>
                    {pictures != null ?
                        <div>
                        <Image
                            preview={{visible: false}}
                            width={400}
                            height={400}
                            src={("http://localhost:8081/product/getPicture?id=" + pictures[0])}
                            onClick={() => setVisible(true)}
                        />
                        <div style={{display: 'none'}}>
                            <Image.PreviewGroup preview={{visible, onVisibleChange: vis => setVisible(vis)}}>
                                {pictures &&
                                pictures.map(picture => {
                                    return (
                                        <Image src={"http://localhost:8081/product/getPicture?id=" + picture}/>
                                    )
                                })}
                            </Image.PreviewGroup>
                        </div>
                        </div> : null
                    }
                </Col>
                <Col offset={1}>
                    Наименование: {product.name + ' ' + product.model} <br/>
                    Описание: {product.desc} <br/>
                    Производитель: {product.manufacturer.name} <br/>
                    Артикул: {product.article} <br/>
                    Дата выхода: {product.dateReleased} <br/>
                    Габариты: {product.dimensions} <br/>
                    Вес: {product.weight} <br/>
                    Рейтинг: <Rate disabled defaultValue={rating}/>
                </Col>
            </Row>
            <Layout style={{padding: '24px 24px 24px'}}>
                <Row>
                    <Col offset={1}>
                        <Descriptions title="Характеристики" bordered={true} column={1}>
                            {charact &&
                            charact.map(char => {
                                    return (
                                        <Descriptions.Item label={char[0]}>{char[1]}</Descriptions.Item>
                                    )
                                }
                            )}
                        </Descriptions>
                    </Col>
                </Row>
            </Layout>
            <Layout style={{padding: '24px 24px 24px'}}>
                <Row>
                    <Col span={10} offset={6}>
                        <Title level={3} align="center">В продаже</Title>
                        <div align="center">
                        <Carousel ref={carousel} align="center">
                            {salesUnit &&
                            salesUnit.map(unit => {
                                    return (
                                        <div style={{border: '1px'}}>
                                            <SalesUnitCarousel unit={unit}/>
                                            <Link to="/salesUnit"><Button onClick={() => GetSUPage(unit.id)}>Перейти</Button></Link>
                                        </div>
                                    )
                                }
                            )}
                        </Carousel>
                        </div>
                        <div align="center">
                            <Button onClick={handlePrev}>Предыдущий магазин</Button>
                            <Button onClick={handleNext}>Следующий магазин</Button>
                        </div>
                    </Col>
                </Row>
            </Layout>
            <Layout style={{padding: '24px 24px 24px'}}>
                <Row>
                    <Col span={10} offset={6}>
                        <Title level={3} align="center">Похожие товары</Title>
                        <div align="center">
                        <Carousel autoplay>
                            {recommended &&
                            recommended.map(recom => {
                                    return (
                                        <div style={{border: '1px'}}>
                                            <ProductCard url={"http://localhost:8081/product/get?id=" + recom.id}/>
                                        </div>
                                    )
                                }
                            )}
                        </Carousel>
                        </div>
                    </Col>
                </Row>
            </Layout>
            <Layout style={{padding: '24px 24px 24px'}}>
                <Row>
                    <Col offset={1}>
                        <Title level={3}>Отзывы</Title>
                        {feedback &&
                        feedback.map(fb => {
                                return (
                                    <Feedback fb={fb}/>
                                )
                            }
                        )}
                        <Form
                            name="basic"
                            labelCol={{
                                span: 8,
                            }}
                            wrapperCol={{
                                span: 16,
                            }}
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={onFinish}
                            autoComplete="off"
                        >
                            <Form.Item
                                label="Текст"
                                name="text"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Введите текст отзыва',
                                    },
                                ]}
                            >
                                <Input.TextArea/>
                            </Form.Item>

                            <Form.Item
                                label="Отметка"
                                name="rating"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Поставьте отметку',
                                    },
                                ]}
                            >
                                <Rate/>
                            </Form.Item>

                            <Form.Item
                                wrapperCol={{
                                    offset: 8,
                                    span: 16,
                                }}
                            >
                                <Button type="primary" htmlType="submit">
                                    Отправить
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </Layout>
        </Content>
    )
    }
}