import { Card, Avatar } from 'antd';
import React from "react";
import { Descriptions } from 'antd';

const { Meta } = Card;

export default function GetCardShop(shop) {
    return (
        <>
            <Card style={{ width: 300, marginTop: 16 }}>
                <Meta
                    title={shop.name}
                />
                {shop.desc}
            </Card>
        </>
    );

}
