import { Descriptions } from 'antd'

export default function ShopInfo(info, shop){
    console.log(shop)
    if(info === "GetInfo") {
        return (
            <Descriptions title="Информация о магазине">
                <Descriptions.Item label="Название">{shop.name}</Descriptions.Item>
                <Descriptions.Item label="Время работы">{shop.workTime}</Descriptions.Item>
                <Descriptions.Item label="Адрес">{shop.address}</Descriptions.Item>
                <Descriptions.Item label="Email">{shop.email}</Descriptions.Item>
                <Descriptions.Item label="Номер телефона">{shop.phoneNumber}</Descriptions.Item>
                <Descriptions.Item label="Описание">{shop.desc}</Descriptions.Item>
            </Descriptions>
        )
    }
}