import { Card, Avatar } from 'antd';
import React from "react";

const { Meta } = Card;

export default function GetCardManufacturer(manufacturer) {
    return (
        <>
            <Card style={{ width: 300, marginTop: 16 }}>
                <Meta
                    avatar={
                        <Avatar src={"http://localhost:8081/manufacturer/getPicture?id=" + manufacturer.id}/>
                    }
                    title={manufacturer.name}
                    description={manufacturer.desc}
                />
            </Card>
        </>
    );

}
