import {Layout, Menu} from "antd";
import {Link, Route} from "react-router-dom";
import {useState} from "react";
import axios from "axios";
import ShopInfo from "./ShopInfo";
import ChangeShopInfo from "./ChangeShopInfo";
import SUForShop from "./SUForShop";
import OrdersForShop from "./OrdersForShop";


export default function ShopPage(page, users){
    const [info, setInfo] = useState("GetInfo");
    const [shop, setShop] = useState();
    const [loading, setLoading] = useState(true)
    if(page === "ShopPage" ) {
        if(shop == null){
            axios.get("http://localhost:8081/shops/findById?id=" + users.shopId).then(response =>{
                    setShop(response.data);
                    setLoading(false);
                }
            )
        }
        if (loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        const {Content, Sider} = Layout;

        const CallGetInfo = async () =>{
            setInfo("GetInfo");
        }

        const CallChangeInfo = async () =>{
            setInfo("ChangeInfo");
        }

        const CallGetSU = async () =>{
            setInfo("ChangePass");
        }

        const CallGetOrders = async () =>{
            setInfo("ChangeEmail");
        }

        return (
            <Layout>
                <Sider width={200} className="site-layout-background">
                    <Menu
                        mode="inline"
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        style={{height: '100%', borderRight: 0}}
                    >
                        <Menu.Item key="1" onClick={CallGetInfo}>  <Link to="/shop">Информация об аккаунте </Link></Menu.Item>
                        <Menu.Item key="2" onClick={CallChangeInfo}>  <Link to="/shop/changeInfoForShop">Изменить данные </Link></Menu.Item>
                        <Menu.Item key="3" onClick={CallGetSU}>  <Link to="/shop/getSalesUnit">Товарные единицы </Link></Menu.Item>
                        <Menu.Item key="4" onClick={CallGetOrders}>  <Link to="/shop/getOrders">Заказы</Link></Menu.Item>
                    </Menu>
                </Sider>
                <Layout style={{padding: '0 24px 24px'}}>
                    <Content
                        className="site-layout-background"
                        style={{
                            padding: 24,
                            margin: 0,
                            minHeight: 280,
                        }}
                    >
                        {ShopInfo(info, shop)}
                        <Route path="/shop/changeInfoForShop">{ChangeShopInfo(info)}</Route>
                        <Route path="/shop/getSalesUnit"><SUForShop shopId={users.shopId}/></Route>
                        <Route path="/shop/getOrders"><OrdersForShop shopId={users.shopId} /></Route>

                    </Content>
                </Layout>
            </Layout>
        )
    }
}