import React from "react";
import axios from "axios";
import {Card, Checkbox, Typography} from "antd";
//axios.get("http://localhost:8081/product/get?id=" + product).then(response => {return (<div>{response.data}</div>)})

export default class SalesUnitCarousel extends React.Component{
    state = {
        shop: [],
        loading: true
    }

    componentDidMount() {
        axios.get("http://localhost:8081/shops/findById?id=" + this.props.unit.shopId)
            .then(res => {
                const shop = res.data;
                this.setState({ shop: shop, loading: false });
            })
    }


    render() {
        if (this.state.loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        const { Title } = Typography;

        return (
            <div>
                <Title level={3}>{this.state.shop.name}</Title>
                <Title level={5}>Описание: {this.props.unit.desc}</Title><br/>
                <Title level={5}>Количество: {this.props.unit.quantity}</Title><br/>
                <Title level={5}>Цена товара: {this.props.unit.price}</Title><br/>
                <Title level={5}>Цена доставки: {this.props.unit.shipCost}</Title><br/>
            </div>
        )

    }

}