import Sider from "antd/es/layout/Sider";
import {Button, Form, Menu, Radio} from "antd";
import Rend from "./Rend";
import axios from "axios";


export default function Characteristics(characts, setProducts, categories){
    const { SubMenu } = Menu;
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 14 },
    };
    const onFinish = (values) =>{
        Object.keys(values).forEach((key) => (values[key] == null) && delete values[key]);
        values.categoryId = categories;
        console.log(values)
        axios.post("http://localhost:8081/product/getInCategory",
            values)
            .then(response => {
                setProducts(response.data)
            })
        //setProducts(values)
        //Написать характеристики
    }
    return(

            <Sider width={200} className="site-layout-background">
                <Form
                    name="validate_other"
                    {...formItemLayout}
                    onFinish={onFinish}
                    initialValues={{
                        'input-number': 3,
                        rate: 3.5,
                    }}
                >
                    <Menu
                        mode="inline"
                        style={{ height: '100%', borderRight: 0 }}
                    >


                        {characts &&
                        characts.map((charact) => {
                            return(
                                <SubMenu key={charact.id} title={charact.name}>
                                    <Form.Item name={charact.name} >
                                        <Radio.Group key={charact.id}>
                                            <Rend url = {"http://localhost:8081/valuesCharacter/getForCharacter?id=" + charact.id} type = {charact.type}/>
                                        </Radio.Group>
                                    </Form.Item>
                                </SubMenu>
                            )
                        })}
                            <Form.Item>
                                <Button type="primary" htmlType="submit">
                                    Применить фильтры
                                </Button>
                            </Form.Item>

                    </Menu>
                </Form>
            </Sider>

    )
}



