import {useState} from "react";
import axios from "axios";
import Characteristics from "./Characteristics";
import {Layout, Space} from "antd";
import ProductCard from "../Product/ProductCard";
import {Link} from "react-router-dom";


export default function CharacteristicsPage(page, characts, categories, CallProductPage, setProduct, products, setProducts) {
    //const [products, setProducts] = useState()
    const [loading, setLoading] = useState(true);

    // if(categories != null && loading){
    //     axios.post("http://localhost:8081/product/getInCategory",
    //         {
    //             categoryId : categories
    //         })
    //         .then(response => {
    //             setProducts(response.data)
    //             setLoading(false)
    //         })
    // }

    if(characts != null && products != null) {

        const ProductPage = (product) => {
            setProduct(product);
            CallProductPage();
            setLoading(true);
        }


        //axios.get("http://localhost:8081/product/getInCategory").then(response => {setProducts(response.data)})
        return (
            <Layout>
                {Characteristics(characts, setProducts, categories)}

                <Layout style={{padding: '24px 24px 24px'}}>
                    <Space wrap>
                        {products &&
                        products.map(product => {
                            return (
                                <Link to="/product">
                                    <div style={{border: '1px'}} onClick={() => ProductPage(product)}>
                                        <ProductCard url={"http://localhost:8081/product/get?id=" + product}/>
                                    </div>
                                </Link>)
                        })}
                    </Space>
                </Layout>
            </Layout>
        )
    }
}