import React, {useEffect, useState} from 'react';
import axios from 'axios';
import 'antd/dist/antd.css';
import {Layout, Menu, Affix, Button} from 'antd';
import {MenuOutlined, SearchOutlined, UserOutlined} from '@ant-design/icons';
import { Dropdown } from 'antd';
import UserPage from "./users/UserPage";
import ManufacturerPage from "./Manufacturer/ManufacturerPage";
import ProductPage from "./Product/ProductPage";
import CategoryMenu from "./Menu/CategoryMenu";
import CharacteristicsPage from "./Characteristic/CharacteristicsPage";
import SalesUnitPage from "./SalesUnit/SalesUnitPage";
import {Link, Redirect, Route, useHistory} from "react-router-dom";
import Search from "antd/es/input/Search";
import SearchPage from "./Search/SearchPage";
import ShopsPage from "./Shops/ShopsPage";
import ShopPage from "./Shops/ShopPage";

export default function App() {
    const { Header } = Layout;
    const [users, setUsers] = useState(null);
    const [page, setPage] = useState("Welcome");
    const [userId, setUserId] = useState();
    const [categories, setCategories] = useState();
    const [characts, setCharacts] = useState();
    const [product, setProduct] = useState();
    const [salesUnits, setSalesUnits] = useState();
    const [search, setSearch] = useState();
    const [products, setProducts] = useState();

    if(userId == null){
        axios.get("http://localhost:8081/")
    }

    useEffect(() => {
        getUserId();
    },[userId])

    const getUserId = () => {
        axios.get('http://localhost:8081/users/whoAmI').then(response => {
            setUserId(response.data)
        });
    }

    useEffect(() => {
        getUser();
    },[userId])

    const getUser = () => {
        axios.get("http://localhost:8081/users/getUserById?id=" + userId).then(response => {
            setUsers(response.data)
        });
    }

    const CallUserPage = async () =>{
        setPage("UserPage");
    }

    const CallShopsPage = async () =>{
        setPage("ShopsPage");
    }

    const CallManufacturerPage = async () =>{
        setPage("ManufacturerPage");
    }

    const CallProductPage = async () =>{
        setPage("ProductPage");
    }

    const CallCategoryPage = async () =>{
        setPage("CategoryMenu");
    }

    const CallSUPage = async () =>{
        setPage("SalesUnitPage");
    }

    const CallCharacteristicsPage = (Category) =>{

        setCategories(Category);
        axios.post("http://localhost:8081/product/getInCategory",
            {
                categoryId : Category
            }).then(response => {
                setProducts(null);
                setProducts(response.data)
        })

        axios.get("http://localhost:8081/characteristic/getAll?id=" + Category)
            .then(response =>{
                setCharacts(response.data)
            })

        setPage("CharacteristicsPage");
    }

    const CallShopPage = async () =>{
        setPage("ShopPage")
    }
    const Logout = async () =>{
        axios.get("http://localhost:8081/logout");
        window.location.replace("http://localhost:8081/login")
    }

    const menuUser = (
        <Menu>
            <Menu.Item key="1" onClick={CallUserPage}>
                <Link to="/user">Профиль</Link>
            </Menu.Item>
            <Menu.Item key="2" onClick={Logout}>
                Выйти
            </Menu.Item>
        </Menu>
    );

    const menuShop = (
        <Menu>
            <Menu.Item key="1" onClick={CallUserPage}>
                <Link to="/user">Профиль</Link>
            </Menu.Item>
            <Menu.Item key="2" onClick={CallShopPage}>
                <Link to="/shop">Магазин</Link>
            </Menu.Item>
            <Menu.Item key="3" onClick={Logout}>
                Выйти
            </Menu.Item>
        </Menu>
    );

    const onSearch = value =>{
        setSearch(value);
    }

    const AccMenu = () =>{
        if(users.shopId == null){
            return(menuUser)
        }else{
            return(menuShop)
        }
    }

    return (
        <div className="usersAPI">
            <Layout>
                <Affix>
                    <Header className="header">
                        <Menu theme="dark" mode="horizontal">
                            <Menu.Item key="1" onClick={CallCategoryPage}>
                                    <Link to="/categoryMenu"><MenuOutlined /> </Link>
                            </Menu.Item>
                            <Menu.Item key="3" onClick={CallShopsPage}> <Link to="/shops">Магазины </Link></Menu.Item>
                            <Menu.Item key="4" onClick={CallManufacturerPage}> <Link to="/manufacturer">Производители </Link></Menu.Item>
                            <Menu.Item key="5" onClick={CallProductPage}><Link to="/product">Товары </Link></Menu.Item>
                            <Menu.Item key="2">
                                    <Search style={{width : 600, margin: 15}} placeholder="input search text" onSearch={onSearch} enterButton={<Link to="/search"><SearchOutlined /> </Link>} />
                            </Menu.Item>

                            <Menu.Item key="6" style={{ marginLeft: 'auto' }}>
                                <Dropdown.Button overlay={AccMenu} placement="bottomCenter" icon={<UserOutlined />}>
                                </Dropdown.Button>
                            </Menu.Item>
                        </Menu>

                    </Header>
                </Affix>
                <Layout>
                    <Route path="/categoryMenu">{CategoryMenu(page, CallCharacteristicsPage)} </Route>
                </Layout>
                <Layout style={{padding: '24px 24px 24px'}}>
                    <Route path="/user">{UserPage(page, users)} </Route>
                    <Route path="/shop">{ShopPage(page, users)} </Route>
                    <Route path="/shops">{ShopsPage(page)} </Route>
                    <Route path="/manufacturer">{ManufacturerPage(page)} </Route>
                    <Route path="/product">{ProductPage(page,product, CallSUPage, setSalesUnits)}</Route>
                    <Route path="/characteristic">{CharacteristicsPage(page, characts, categories, CallProductPage, setProduct, products, setProducts)}</Route>
                    <Route path="/salesUnit">{SalesUnitPage(page, salesUnits, userId, CallUserPage)} </Route>
                    <Route path="/search">{SearchPage(search, setProduct, CallProductPage)}</Route>
                </Layout>
            </Layout>
        </div>
    );
}