import {Button, Card, Space} from "antd";
import {Link} from "react-router-dom";


export default function SubCategoryMenu(CallCharacteristicsPage) {

    return (
        <Space size={[8, 16]} wrap>
            <Card title="Смартфоны" bordered={false} style={{width: 300}}>
                <Button type="text"><Link to="/characteristic">Премиум смартфоны</Link></Button>
                <Button type="text" onClick={() => CallCharacteristicsPage(3)}><Link to="/characteristic">Apple iPhone</Link></Button>
                <Button type="text" onClick={() => CallCharacteristicsPage(4)}><Link to="/characteristic">Смартфоны Samsung</Link></Button>
                <Button type="text">Смартфоны Xiaomi</Button>
                <Button type="text">Недорогие смартфоны</Button>
                <Button type="text">Мощные смартфоны</Button>
                <Button type="text">Смартфоны на Android</Button>
                <Button type="text">Смартфоны с хорошей камерой</Button>
            </Card>
            <Card title="Аксессуары для смартфонов" bordered={false} style={{width: 300}}>
                <Button type="text">Зарядные устройства для телефонов</Button>
                <Button type="text">Кабель USB Type-C</Button>
                <Button type="text">Кабель Lightning</Button>
                <Button type="text">Кабель micro USB</Button>
                <Button type="text">Штативы-моноподы</Button>
                <Button type="text">Штативы трипод</Button>
                <Button type="text">Внешние аккумуляторы (Power Bank)</Button>
                <Button type="text">Стедикамы</Button>
                <Button type="text">Карты памяти</Button>
                <Button type="text">Защитные пленки и стекла</Button>
                <Button type="text">Чехлы для смартфонов</Button>
                <Button type="text">Селфи-палки</Button>
                <Button type="text">Внешние моды для смартфонов</Button>
                <Button type="text">Портативные колонки</Button>
                <Button type="text">Держатели для смартфонов</Button>
                <Button type="text">SIM карты (тарифные планы)</Button>
                <Button type="text">Смарт-трекеры</Button>
            </Card>
            <Card title="Наушники" bordered={false} style={{width: 300}}>
                <Button type="text"><Link to="/characteristic">Мониторные наушники</Link></Button>
                <Button type="text">Наушники True Wireless</Button>
                <Button type="text">Беспроводные наушники</Button>
                <Button type="text">Наушники вкладыши</Button>
                <Button type="text">Проводные наушники</Button>
                <Button type="text">Наушники Beats</Button>
                <Button type="text">Наушники DIGMA</Button>
                <Button type="text">Наушники Oklick</Button>
                <Button type="text">Накладные наушники</Button>
                <Button type="text">Недорогие наушники</Button>
                <Button type="text">Спортивные наушники</Button>
            </Card>
            <Card title="Аксессуары для планшетов" bordered={false} style={{width: 300}}>
                <Button type="text">Зарядные устройства для планшета</Button>
                <Button type="text">Клавиатуры для планшетов</Button>
                <Button type="text">Кабели для смартфонов и планшетов</Button>
                <Button type="text">Чехлы для планшетов</Button>
                <Button type="text">Защитные пленки для планшетов</Button>
                <Button type="text">Док-станции для планшетов</Button>
                <Button type="text">Держатели, подставки</Button>
            </Card>
            <Card title="Гаджеты" bordered={false} style={{width: 300}}>
                <Button type="text">Экшн камеры</Button>
                <Button type="text">Квадрокоптеры</Button>
                <Button type="text">Аксессуары для квадрокоптеров</Button>
                <Button type="text">Цифровые фоторамки</Button>
                <Button type="text">Виртуальные очки</Button>
                <Button type="text">Смарт-часы</Button>
                <Button type="text">Фитнес-браслеты</Button>
                <Button type="text">Аксессуары для гаджетов</Button>
            </Card>
            <Card title="Зарядные устройства" bordered={false} style={{width: 300}}>
                <Button type="text">Зарядные устройства для телефонов</Button>
                <Button type="text">Зарядные устройства для планшета</Button>
                <Button type="text">Автомобильные зарядные устройства</Button>
                <Button type="text">Беспроводные зарядные устройства</Button>
            </Card>
            <Card title="Электронные книги и аксессуары" bordered={false} style={{width: 300}}>
                <Button type="text">Электронные книги</Button>
                <Button type="text">Аксессуары для электронных книг</Button>
            </Card>
            <Card title="Сотовые телефоны" bordered={false} style={{width: 300}}>
                <Button type="text">Дешевые мобильные телефоны</Button>
            </Card>
            <Card title="Планшеты" bordered={false} style={{width: 300}}>
                <Button type="text">Детские планшеты</Button>
                <Button type="text">Недорогие планшеты</Button>
                <Button type="text">Планшеты Digma</Button>
                <Button type="text">Игровые планшеты</Button>
            </Card>
            <Card title="Усилители для наушников" bordered={false} style={{width: 300}}>
            </Card>
            <Card title="Аксессуары для наушников" bordered={false} style={{width: 300}}>
            </Card>
        </Space>
    )
}