import { Descriptions } from 'antd'

export default function GetInfo(info, users){
    if(info === "GetInfo") {
        return (
            <Descriptions title="Информация об аккаунте">
                <Descriptions.Item label="Имя пользователя">{users.username}</Descriptions.Item>
                <Descriptions.Item label="ФИО">{users.fcs}</Descriptions.Item>
                <Descriptions.Item label="Город">{users.city}</Descriptions.Item>
                <Descriptions.Item label="Адрес" span={2}>
                    {users.address}
                </Descriptions.Item>
                <Descriptions.Item label="Email">{users.email}</Descriptions.Item>
                <Descriptions.Item label="Номер телефона">{users.phoneNumber}</Descriptions.Item>
                <Descriptions.Item label="Магазин">{users.shopId}</Descriptions.Item>
                <Descriptions.Item label="Статус активации">{users.activation}</Descriptions.Item>
            </Descriptions>
        )
    }
}