import {Drawer, Form, Button, Col, Row, Input, Select, Space, Checkbox} from 'antd';
import React from "react";
import axios from "axios";
import {Redirect} from "react-router-dom";

export default class CreateOrder extends React.Component {
    state = { visible: false, delivery: false };

    showDrawer = () => {
        this.setState({
            visible: true,
        });
    };

    onClose = () => {
        this.setState({
            visible: false,
        });
    };

    render() {
        const onChange = () =>{
            this.state.delivery = !this.state.delivery;
        }

        const onFinish = (values) => {
            values.userId = this.props.userId
            values.suId = this.props.suID
            if(values.mark){
                values.mark = 1
            }else{
                values.mark = 0
            }
            axios.put("http://localhost:8081/order/save", values)
            window.location.replace("http://localhost:8081/");
        }
        return (
            <>
                <Button type="primary" onClick={this.showDrawer}>
                    Купить
                </Button>
                <Drawer
                    title="Заказать"
                    width={720}
                    onClose={this.onClose}
                    visible={this.state.visible}
                    bodyStyle={{ paddingBottom: 80 }}
                    extra={
                        <Space>
                            <Button onClick={this.onClose}>Cancel</Button>
                            <Button onClick={this.onClose} type="primary">
                                Submit
                            </Button>
                        </Space>
                    }
                >
                    <Form layout="vertical" hideRequiredMark onFinish={onFinish}>
                        <Row gutter={16}>
                            <Col span={12}>
                                <Form.Item
                                    name="address"
                                    label="Адрес доставки"
                                >
                                    <Input placeholder="Адрес" />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item
                                    name="mark"
                                    label="Доставка курьером"
                                    valuePropName="checked"
                                >
                                    <Checkbox onChange={onChange}/>
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={16}>
                            <Col span={24}>
                                <Form.Item
                                    name="comm"
                                    label="Комментарий к заказу"
                                >
                                    <Input.TextArea rows={4} placeholder="Введите комментарий" />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={16}>
                            <Col>
                                <Form.Item
                                    wrapperCol={{
                                        offset: 8,
                                        span: 16,
                                    }}
                                >
                                    <Button type="primary" htmlType="submit">
                                        Отправить
                                    </Button>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Drawer>
            </>
        );
    }
}