import React from "react";
import axios from "axios";
import {Card, Checkbox} from "antd";
//axios.get("http://localhost:8081/product/get?id=" + product).then(response => {return (<div>{response.data}</div>)})

export default class ProductCard extends React.Component{
    state = {
        product: [],
        loading: true
    }

    componentDidMount() {
        axios.get(this.props.url)
            .then(res => {
                const product = res.data;
                this.setState({ product: product, loading: false });
            })
    }


    render() {
        if (this.state.loading) {
            return (<p>Loading...</p>); // отображение загрузки
        }

        const { Meta } = Card;

            if(this.state.product.picture != null) {
                return (
                    <Card
                        hoverable
                        style={{width: 240}}
                        cover={<img style={{width: 240, height: 400}} alt="example" src={"http://localhost:8081/product/getPicture?id=" + this.state.product.picture.id}/>}
                    >
                        <Meta title={this.state.product.name + ' ' + this.state.product.model}
                              description={this.state.product.desc}/>
                        Производитель: {this.state.product.manufacturer.name} <br/>
                        Артикул: {this.state.product.article} <br/>
                        Дата выхода: {this.state.product.dateReleased} <br/>
                        Габариты: {this.state.product.dimensions} <br/>
                        Вес: {this.state.product.weight}
                    </Card>
                )
            }
            else {
                return (
                    <Card
                        hoverable
                        style={{width: 240}}
                        cover={<img alt="example" src={"http://localhost:8081/product/getPicture?id=" }/>}
                    >
                        <Meta title={this.state.product.name + ' ' + this.state.product.model}
                              description={this.state.product.desc}/>
                        Производитель: {this.state.product.manufacturer.name} <br/>
                        Артикул: {this.state.product.article} <br/>
                        Дата выхода: {this.state.product.dateReleased} <br/>
                        Габариты: {this.state.product.dimensions} <br/>
                        Вес: {this.state.product.weight}
                    </Card>
                )
            }

    }

}