import {Layout, Menu} from 'antd';
import Sider from "antd/es/layout/Sider";
import {Content} from "antd/lib/layout/layout";
import SubCategoryMenu from "./SubCategoryMenu";

export default function CategoryMenu(page, CallCharacteristicsPage) {
    return (
        <Layout>
            <Sider width={350} className="site-layout-background">
                <Menu
                    mode="inline"
                    defaultSelectedKeys={['1']}
                    style={{height: '100%', borderRight: 0}}
                >
                    <Menu.Item key="1">Смартфоны и гаджеты</Menu.Item>
                    <Menu.Item key="2">Ноутбуки и компьютеры</Menu.Item>
                    <Menu.Item key="3">Телевизоры, аудио-видео, Hi-Fi</Menu.Item>
                    <Menu.Item key="4">Бытовая техника для дома и кухни</Menu.Item>
                    <Menu.Item key="5">Строительство и ремонт</Menu.Item>
                    <Menu.Item key="6">Дом и дача</Menu.Item>
                    <Menu.Item key="7">Умный дом и системы безопасности</Menu.Item>
                    <Menu.Item key="8">Автотовары</Menu.Item>
                    <Menu.Item key="9">Канцтовары, Мебель и Офисная техника</Menu.Item>
                    <Menu.Item key="10">Красота и здоровье</Menu.Item>
                    <Menu.Item key="11">Детские товары</Menu.Item>
                    <Menu.Item key="13">Спорт и отдых</Menu.Item>
                    <Menu.Item key="14">Товары для геймеров</Menu.Item>
                    <Menu.Item key="15">Уцененные товары</Menu.Item>
                    <Menu.Item key="16">Подарочные сертификаты</Menu.Item>
                    <Menu.Item key="17">Сервисы и услуги</Menu.Item>
                </Menu>
            </Sider>
            <Content
                className="site-layout-background"
                style={{
                    padding: 24,
                    margin: 0,
                    minHeight: 280,
                }}>
                {SubCategoryMenu(CallCharacteristicsPage)}
            </Content>
        </Layout>
    )

}